<template>
  <div class="checkout-page">
    <div class="container">
      <div class="page-header">
        <h1>订单结算</h1>
      </div>
      
      <el-steps :active="currentStep" finish-status="success" class="checkout-steps">
        <el-step title="确认订单"></el-step>
        <el-step title="选择支付方式"></el-step>
        <el-step title="完成下单"></el-step>
      </el-steps>
      
      <!-- 第一步：确认订单 -->
      <div v-if="currentStep === 0">
        <h3 class="section-title">确认商品信息</h3>
        <el-table :data="cartItems" style="width: 100%" :header-cell-style="{ background: '#f5f7fa' }">
          <el-table-column label="商品信息" min-width="400">
            <template #default="{ row }">
              <div class="product-info">
                <el-image :src="getFullImageUrl(row.image_url)" :alt="row.name" class="product-image"></el-image>
                <div class="product-details">
                  <h4>{{ row.name }}</h4>
                  <div v-if="row.eco_certification || row.is_recyclable" class="product-tags">
                    <el-tag v-if="row.eco_certification" size="small" type="success">
                      {{ row.eco_certification }}
                    </el-tag>
                    <el-tag v-if="row.is_recyclable" size="small" type="info">可回收</el-tag>
                  </div>
                </div>
              </div>
            </template>
          </el-table-column>
          
          <el-table-column label="单价" align="center" width="120">
            <template #default="{ row }">
              <span class="price">¥{{ row.price.toFixed(2) }}</span>
            </template>
          </el-table-column>
          
          <el-table-column label="数量" align="center" width="100">
            <template #default="{ row }">
              <span>x {{ row.quantity }}</span>
            </template>
          </el-table-column>
          
          <el-table-column label="小计" align="center" width="120">
            <template #default="{ row }">
              <span class="subtotal">¥{{ (row.price * row.quantity).toFixed(2) }}</span>
            </template>
          </el-table-column>
        </el-table>

        <h3 class="section-title">收货地址</h3>
        <el-card class="selected-address-card" @click="addressDialogVisible = true">
          <div v-if="selectedAddress">
            <div>{{ selectedAddress.name }} {{ selectedAddress.phone }}</div>
            <div>{{ selectedAddress.province }} {{ selectedAddress.city }} {{ selectedAddress.district }} {{ selectedAddress.detail }}</div>
            <div v-if="selectedAddress.is_default" class="default-tag">默认地址</div>
          </div>
          <div v-else>请选择地址</div>
        </el-card>

        <el-dialog v-model="addressDialogVisible" title="选择收货地址" width="600px">
        <el-radio-group v-model="selectedAddressId" class="address-radio-group">
          <el-card
            v-for="(addr, index) in addressList"
            :key="addr.id"
            class="address-item"
            :class="{ selected: addr.id === selectedAddressId }"
          >
            <el-radio :label="addr.id">
              {{ addr.name }} {{ addr.phone }}<br />
              {{ addr.province }} {{ addr.city }} {{ addr.district }} {{ addr.detail }}
              <span v-if="addr.is_default" class="default-tag">默认</span>
            </el-radio>
          </el-card>
        </el-radio-group>
        
        <template #footer>
          <el-button @click="addressDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="confirmSelectAddress">确定</el-button>
        </template>
      </el-dialog>

        
        <div class="step-actions">
          <el-button @click="$router.push('/cart')">返回购物车</el-button>
          <el-button type="primary" @click="currentStep = checkCartStatus() ? 1 : 0">下一步</el-button>
        </div>
      </div>
      
      <!-- 第二步：选择支付方式 -->
      <div v-if="currentStep === 1">
        <h3 class="section-title">订单信息</h3>
        <el-card class="order-summary">
          <div class="summary-item">
            <span>商品总价：</span>
            <span class="price">¥{{ cartTotal.toFixed(2) }}</span>
          </div>
          <div class="summary-item">
            <span>运费：</span>
            <span>¥{{ shipping.toFixed(2) }}</span>
          </div>
          <div class="summary-item">
            <span>优惠金额：</span>
            <span>-¥{{ discount.toFixed(2) }}</span>
          </div>
          <div class="summary-item total">
            <span>应付总额：</span>
            <span class="price">¥{{ orderTotal.toFixed(2) }}</span>
          </div>
        </el-card>
        
        <h3 class="section-title">选择支付方式</h3>
        <el-card class="payment-card">
          <el-radio-group v-model="paymentMethod">
            <div class="payment-option">
              <el-radio label="alipay">支付宝</el-radio>
              <div class="payment-icon alipay-icon"></div>
            </div>
            <div class="payment-option">
              <el-radio label="wechat">微信支付</el-radio>
              <div class="payment-icon wechat-icon"></div>
            </div>
            <div class="payment-option">
              <el-radio label="creditcard">银行卡支付</el-radio>
              <div class="payment-icon card-icon">
                <i class="el-icon-bank-card"></i>
              </div>
            </div>
          </el-radio-group>
        </el-card>
        
        <div class="step-actions">
          <el-button @click="currentStep = 0">上一步</el-button>
          <el-button type="primary" @click="submitOrder">提交订单</el-button>
        </div>
      </div>
      
      <!-- 第三步：下单完成 -->
      <div v-if="currentStep === 2">
        <div class="order-success">
          <el-result
            icon="success"
            title="订单提交成功!"
            sub-title="您的订单已提交成功，我们将尽快为您处理">
          </el-result>
          
          <div class="order-detail-card">
            <h3>订单信息</h3>
            <div class="order-detail-item">
              <span class="detail-label">订单号：</span>
              <span class="detail-value">{{ orderNumber }}</span>
            </div>
            <div class="order-detail-item">
              <span class="detail-label">下单时间：</span>
              <span class="detail-value">{{ orderTime }}</span>
            </div>
            <div class="order-detail-item">
              <span class="detail-label">订单金额：</span>
              <span class="detail-value price">¥{{ finalAmount.toFixed(2) }}</span>
            </div>
            <div class="order-detail-item">
              <span class="detail-label">支付方式：</span>
              <span class="detail-value">{{ paymentMethod === 'alipay' ? '支付宝' : 
                                          paymentMethod === 'wechat' ? '微信支付' : '银行卡支付' }}</span>
            </div>
          </div>
          
          <div class="success-actions">
            <el-button type="primary" @click="$router.push('/orders')">查看订单</el-button>
            <el-button @click="$router.push('/')">返回首页</el-button>
          </div>
          
          <div class="order-tips">
            <h4>温馨提示：</h4>
            <p>1. 您的订单已提交成功，请您立即支付，我们将尽快为您安排发货。</p>
            <p>2. 如需修改订单信息，请尽快联系客服。</p>
            <p>3. 欢迎在收到商品后对商品进行评价，您的意见对我们非常重要。</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import { getFullImageUrl } from '../utils/index'
import { getAddressList } from '@/api/address'  // 确保你已创建 address.js 并导出 getAddressList
import axios from 'axios'

export default {
  name: 'Checkout',
  setup() {
    const store = useStore()
    const router = useRouter()

    const currentStep = ref(0)
    const cartItems = computed(() => store.getters['cart/cartItems'])
    const cartTotal = computed(() => store.getters['cart/cartTotal'])
    const cartCount = computed(() => store.getters['cart/cartCount'])
    const shipping = ref(10.00)
    const discount = ref(0.00)
    const orderTotal = computed(() => cartTotal.value + shipping.value - discount.value)
    const paymentMethod = ref('alipay')
    const orderNumber = ref('')
    const orderTime = ref('')
    const finalAmount = ref(0)

    const addressList = ref([])
    const selectedAddress = ref(null)
    const selectedAddressId = ref(null)
    const addressDialogVisible = ref(false)

    const loadAddresses = async () => {
      try {
        const res = await getAddressList()
        addressList.value = res
        if (res.length > 0) {
          selectedAddress.value = res.find(a => a.is_default) || res[0]
        }
      } catch (e) {
        ElMessage.error('加载地址失败')
      }
    }

    const checkCartStatus = () => {
      if (cartItems.value.length === 0) {
        ElMessage.warning('您的购物车为空，请先添加商品')
        router.push('/products')
        return false
      }
      const outOfStockItems = cartItems.value.filter(item => item.stock <= 0)
      if (outOfStockItems.length > 0) {
        const itemNames = outOfStockItems.map(item => item.name).join('、')
        ElMessage.warning(`商品「${itemNames}」缺货，请从购物车中移除后再结算`)
        router.push('/cart')
        return false
      }
      return true
    }

    const confirmSelectAddress = () => {
      const found = addressList.value.find(item => item.id === selectedAddressId.value)
      if (found) {
        selectedAddress.value = found
        addressDialogVisible.value = false
      } else {
        ElMessage.warning('请选择地址')
      }
    }

    onMounted(() => {
      checkCartStatus()
      loadAddresses()
    })

    const generateOrderNumber = () => {
      const date = new Date()
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const random = Math.floor(Math.random() * 10000).toString().padStart(4, '0')
      return `${year}${month}${day}${random}`
    }

    const formatOrderTime = () => {
      try {
        const date = new Date()
        const year = date.getFullYear()
        const month = String(date.getMonth() + 1).padStart(2, '0')
        const day = String(date.getDate()).padStart(2, '0')
        const hours = String(date.getHours()).padStart(2, '0')
        const minutes = String(date.getMinutes()).padStart(2, '0')
        return `${year}-${month}-${day} ${hours}:${minutes}`
      } catch (err) {
        return new Date().toLocaleString('zh-CN')
      }
    }

    const submitOrder = () => {
      if (!paymentMethod.value) {
        ElMessage.warning('请选择支付方式')
        return
      }

      if (cartItems.value.length === 0) {
        ElMessage.warning('购物车为空，无法提交订单')
        return
      }

      const fixedItems = cartItems.value.map(item => {
        const fixedItem = { ...item }
        if (!fixedItem.id && !fixedItem.product_id) {
          if (fixedItem._id) fixedItem.id = fixedItem._id
          else if (fixedItem.productId) fixedItem.id = fixedItem.productId
          else fixedItem.id = `temp_${Date.now()}_${fixedItem.name}`
        }
        fixedItem.id = parseInt(fixedItem.id)
        return fixedItem
      }).filter(item => Number.isInteger(item.id) && item.id > 0)

      if (fixedItems.length === 0) {
        ElMessage.warning('没有有效商品可提交')
        return
      }

      if (orderTotal.value <= 0) {
        ElMessage.warning('订单金额异常，请刷新页面重试')
        return
      }

      ElMessageBox.confirm(`确认提交订单 ¥${orderTotal.value.toFixed(2)} ?`, '确认订单', {
        confirmButtonText: '确认提交',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        try {
          const rawToken = localStorage.getItem('token')
          if (!rawToken || rawToken === 'null' || rawToken === 'undefined') {
            ElMessage.error('用户未登录，请先登录')
            return
          }

          const token = `Bearer ${rawToken}`

          const orderData = {
            address_id: selectedAddress.value?.id || 0,
            items: fixedItems.map(item => ({
              product_id: item.id,
              quantity: parseInt(item.quantity)
            }))
          }

          const response = await axios.post('/api/orders/create', orderData, {
            headers: {
              Authorization: token,
              'Content-Type': 'application/json'
            }
          })

          if (response && response.data && response.data.code === 200) {
            finalAmount.value = orderTotal.value
            orderNumber.value = response.data.data.order_number || generateOrderNumber()
            orderTime.value = formatOrderTime()
            store.dispatch('cart/clearCart')
            currentStep.value = 2
            ElMessage.success('订单提交成功')
          } else {
            throw new Error(response?.data?.msg || '订单提交失败')
          }
        } catch (error) {
          console.error('提交订单失败:', error)
          ElMessage.error(error.message || '订单提交失败，请重试')
        }
      }).catch(() => {})
    }

    onMounted(() => {
      checkCartStatus()
    })

    return {
      currentStep,
      cartItems,
      cartTotal,
      cartCount,
      shipping,
      discount,
      orderTotal,
      paymentMethod,
      submitOrder,
      getFullImageUrl,
      orderNumber,
      orderTime,
      finalAmount,
      checkCartStatus,
      selectedAddress,
      selectedAddressId,
      addressDialogVisible,
      addressList,
      confirmSelectAddress
    }
  }
}
</script>

  
